<template>
	<div>
		<input @keydown.enter="addTask" :class="['edit' ,{ 'focus' : isFocus}]" type="text" @focus="isFocus = ! isFocus" @blur="isFocus = ! isFocus" v-model="task.content" placeholder="请添加任务">
		<div class="list">
			<div class="unit" v-for="(item,index) in list">
				<input class="checkbox" @click="changeState(index)" :checked="item.finished" type="checkbox">
				<span :class="{'finish':item.finished}">
  {{item.content}}
</span>
				<button @click="removeTask(index)" class="del">delete</button>
			</div>
			<p v-if="list.length === 0" class="empty">
				暂无任务
			</p>
		</div>
	</div>
</template>

<script>
	export default {
		name: 'first',
		data() {
			return {
				//默认
				task: {
					content: '', //内容为空
					finished: false, //未完成
					deleted: false //未删除
				},
				//任务列表
				list: [],
				isFocus: false,
			}
		},
		methods: {
			//添加任务
			addTask: function() {
				//将task存入list数组
				this.list.push(this.task);
				//存入list[]后，重置task
				this.task = {
					content: '', //内容为空
					finished: false, //未完成
					deleted: false //未删除
				}
			},
			removeTask: function(index) {
				//使用splice操作删除数组指定项
				this.list.splice(index, 1);
			},
			//点击修改任务状态
			changeState: function(index) {
				let curState = this.list[index].finished;
				this.list[index].finished = !curState;
			}

		}
	}
</script>

<style>
			.edit {
				display: block;
				width: 80%;
				height: 150px;
				font-size: 80px;
				line-height: 85px;
				margin: 80px auto 30px;
				box-sizing: border-box;
				padding-left: 4px;
				border-radius: 4px;
				border: 1px solid #ccc;
				outline: 0;
				box-shadow: 0 0 5px #ccc;
			}
			.focus{
				box-shadow: 0 0 50px #BF5;
				
			}
			
			.list {
				margin: 115px auto 0;
				width: 80%;
			}
			
			.unit {
				font-size: 80px;
				position: relative;
				padding: 10px 0;
				border-bottom: 1px solid #efefef;
			}
			
			.unit:last-child {
				border-bottom: 0;
			}
			
			.checkbox{
				width: 70px;
				height: 70px;
			}
			.finish{
  text-decoration: line-through;
  color: #ccc;
}
.del{
	
width: 168px;
height: 70px;
line-height: 70px;
  transform: translateY(16px);
  background: red;
  text-decoration: none;
  position: absolute;
  right:0;
  font-size: 50px;
  border: 0;
  outline: 0;
  border-radius: 20px;
  color: #fff;
  font-weight: bold;
}
.empty{
  font-size: 50px;
  color: #ccc;
  text-align: center;
  padding: 50px 0;
}
</style>



